//Providers.jsMetaMaskethers
import { ethers } from 'ethers';
export default class Providers {
    static isMetaMaskProvider () {
        return Boolean( window.web3 && window.web3.currentProvider );
    }
    static createProvider () {
        if (Providers.isMetaMaskProvider()) {
            // MetaMask
            return new ethers.providers.Web3Provider( window.web3.currentProvider );
        } 
		else {
            // InfuraProviderinfura.ioTokennetworkropsten3
            // return new ethers.providers.InfuraProvider(
            //     3, 'ea9fcb17397d4e2994424dcd677d2a2f' );
            // infura.ioropsten(https,)
            // JsonRpcProviderurl 
            // return new ethers.providers.JsonRpcProvider(
            //     'https://mainnet.infura.io/v3/36134feb3a344c0a923d3e9c908eb581');
			return new ethers.providers.InfuraProvider(
			    3, '36134feb3a344c0a923d3e9c908eb581' );
        }
	}
}



